* {
    margin: 0;
    box-sizing: border-box;
}
nav {
    background-color: darkgray;
    position: fixed;
    width: 100%;
    padding: 5px;

    a {
        color: white;
        text-decoration: none;
        padding: 5px;
    }
}

.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url('/img/welcome.jpg');
    width: 100%;
    height: 50vh;

    h1 {
       font-size: 400%; 
       padding-bottom: 5px;
       color: white;
       padding-top: 80px;
    }
    h2 {
        padding-bottom: 50px;
        color: white;
    }
}

.plot {
    background-color: lightgray;
    display: flex;
    flex-direction: column;
    padding-top: 100px;

    h2 {
        text-align: center;
        font-size: 200%;
        padding-bottom: 40px;
    }
}
.plot img {
    height: 50%;
    width: 50%;
    padding-right: 5px;
}
.plotcontent {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-left: 20vw;
    padding-right: 20vw;
}
.plotcontentwords {
    display: flex;
    flex-direction: column;
}